#login-main form,
#addfriend-main form,
#register-main form,
#resetpassword-main form {
  margin: 2rem auto;
  height: 30rem;
  width: 26%;
  padding: 2rem 0.5rem;
}
.auth-forms-h2 {
  padding: 0.5rem 1.25rem;
}
.auth-forms-hr {
  width: 90%;
  border: none;
  border-top: 1px solid rgb(48, 48, 52);
}
.auth-forms-middle-cont {
  width: 88%;
  height: 75%;
  margin: 0 auto;
  margin-top: 0.5rem;
}
.auth-forms-inputs {
  width: 100%;
  height: 1.5rem;
  margin: 0.3rem 0;
}
.auth-forms-btn {
  margin-bottom: 0.5rem;
}
@media only screen and (max-width: 790px) {
  #login-main form,
  #addfriend-main form,
  #register-main form,
  #resetpassword-main form {
    width: 90%;
  }
}
